@import url(../libs/slick-carousel/slick/slick.css);
@import url(../libs/animate.css/animate.min.css);
.btn {
  position: relative;
  padding: 3px 20px;
  display: flex;
  align-items: center;
  border: 2px solid #ffffff;
  white-space: nowrap;
  overflow: hidden;
  transition: .5s;
}

.btn::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  background-color: #fff;
  transition: .8s;
  z-index: -1;
  border-radius: 0 0 50% 50%;
}

.btn svg {
  margin-right: 15px;
}

.btn:hover {
  border: 2px solid #568f0a;
  color: #568F0A;
  cursor: pointer;
}

.btn:hover::before {
  height: 180%;
}

.btn:hover svg use {
  fill: #568F0A;
}

@font-face {
  font-family: "GothamPro";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/GothamProLight/GothamProLight.eot");
  src: url("../fonts/GothamProLight/GothamProLight.eot?#iefix") format("embedded-opentype"), url("../fonts/GothamProLight/GothamProLight.woff") format("woff"), url("../fonts/GothamProLight/GothamProLight.ttf") format("truetype");
}

@font-face {
  font-family: "GothamPro";
