::placeholder {
  color: #666;
}

::selection {
  background-color: #568F0A;
  color: #fff;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.65;
  font-family: "GothamPro", sans-serif;
  overflow-x: hidden;
  color: #606060;
  background-color: #eaeaea;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

b, strong {
  font-weight: bold;
}

img {
  width: 100%;
}

img:focus {
  outline: none;
}

a {
  text-decoration: none;
}

a:hover {
  cursor: pointer;
}

button {
  border: none;
  text-decoration: none;
}

button:hover {
  cursor: pointer;
}

.button {
  margin: 0 auto;
  display: block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #568F0A;
  padding: 15px 45px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 3px;
  border-radius: 2px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .1s ease;
  cursor: pointer;
}

.button::after {
  transition: background-color .2s ease;
  position: absolute;
  content: '';
  height: 4px;
  bottom: 0;
  width: 100%;
  background-color: black;
  opacity: .18;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  left: 0;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: #64a70c;
}

.button:hover::after {
  opacity: .22;
}

.button:active {
  background-color: #487708;
}

.button:active::after {
  opacity: .32;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 780px) {
  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(6.66667% - 20px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.productItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: space-around;
}

.productItem {
  padding: 20px;
  margin: 20px 0;
  border-radius: 5px;
  background-color: #fff;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(26.66667% - 20px);
}

@media screen and (max-width: 960px) {
  .productItem {
    width: calc(40% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .productItem {
    width: calc(80% - 20px);
  }
}

.productItem__title {
  margin-top: 15px;
  color: #568F0A;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
}

.productItem__descr {
  margin: 15px 0;
  font-size: 18px;
  font-weight: 400;
}

.productItem button {
  font-weight: 300;
}

.top-menu-left {
  min-height: 130px;
  display: flex;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
}

.top-menu-right {
  min-width: 440px;
  justify-content: space-between;
  display: flex;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
}

.logo {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
}

.header {
  position: relative;
  min-height: 90px;
  color: #ffffff;
  font-family: "GothamPro";
  font-size: 14px;
  font-weight: 500;
  line-height: 31px;
  letter-spacing: 0.14px;
}

.header-section {
  background-color: #568F0A;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.header a {
  color: #fff;
}

.header .webcamera {
  padding: 4px 30px;
  margin-right: 33px;
  display: flex;
  align-items: center;
  background-color: #689a23;
}

.header .webcamera svg {
  margin-right: 15px;
}

.header .logo {
  width: 290px;
  height: 170px;
  background: #568F0A url("../img/logo.svg") center center no-repeat;
}

.header .gallery {
  display: flex;
}

.header .gallery .wrap-icon {
  margin-right: 15px;
  position: relative;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  display: block;
  border-radius: 50%;
  background-color: #689a23;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .tellink {
  white-space: nowrap;
}

.header .menu-icon {
  user-select: none;
  transition: .25s ease-in-out;
  overflow: hidden;
}

.header .menu-icon:hover {
  cursor: pointer;
}

.header .menu-icon.menu-active .top {
  width: 0%;
  transition-delay: 0s;
  transition: .25s ease-in-out;
}

.header .menu-icon.menu-active .middle {
  width: 0%;
  transition-delay: 0.125s;
  transition: .35s ease-in-out;
}

.header .menu-icon.menu-active .bottom {
  width: 0%;
  transition-delay: 0.250s;
  transition: .45s ease-in-out;
}

.header .menu-icon.menu-active .leaves {
  transform: translateX(100%);
  transition-delay: 0.350s;
  transition: .45s ease-in-out;
}

.header .menu-icon.menu-active .word .m {
  transform: translateY(-100%);
  transition-delay: 0s;
  transition: .45s ease-in-out;
}

.header .menu-icon.menu-active .word .e {
  transform: translateY(-100%);
  transition-delay: 0.125s;
  transition: .55s ease-in-out;
}

.header .menu-icon.menu-active .word .n {
  transform: translateY(-100%);
  transition-delay: 0.250s;
  transition: .65s ease-in-out;
}

.header .menu-icon.menu-active .word .u {
  transform: translateY(-100%);
  transition-delay: 0.300s;
  transition: .75s ease-in-out;
}

.header .menu-icon.menu-active .cross_1 {
  width: 100%;
  transform: rotate(45deg) translateY(49%) translateX(2%);
  transform-origin: 50% 50%;
  transition-delay: 1.625s;
  transition: .25s ease-in-out;
}

.header .menu-icon.menu-active .cross_2 {
  width: 100%;
  transform: rotate(-45deg) translateY(49%) translateX(-1%);
  transform-origin: 50% 50%;
  transition-delay: 1.375s;
  transition: .25s ease-in-out;
}

.header .menu-icon .top {
  transition-delay: 0.5s;
  transition: .25s ease-in-out;
}

.header .menu-icon .middle {
  transition-delay: 0.625s;
  transition: .35s ease-in-out;
}

.header .menu-icon .bottom {
  transition-delay: 0.750s;
  transition: .45s ease-in-out;
}

.header .menu-icon .leaves {
  transform: translateX(0);
  transition-delay: 0.350s;
  transition: .45s ease-in-out;
}

.header .menu-icon .word .m {
  transform: translateY(0);
  transition-delay: 0s;
  transition: .45s ease-in-out;
}

.header .menu-icon .word .e {
  transform: translateY(0);
  transition-delay: 0.125s;
  transition: .55s ease-in-out;
}

.header .menu-icon .word .n {
  transform: translateY(0);
  transition-delay: 0.250s;
  transition: .65s ease-in-out;
}

.header .menu-icon .word .u {
  transform: translateY(0);
  transition-delay: 0.300s;
  transition: .75s ease-in-out;
}

.header .menu-icon .cross_1 {
  width: 0%;
  transform: rotate(45deg) translateY(49%);
  transform-origin: 50% 50%;
  transition-delay: 1.375s;
  transition: .25s ease-in-out;
}

.header .menu-icon .cross_2 {
  width: 0%;
  transform: rotate(-45deg) translateY(49%);
  transform-origin: 50% 50%;
  transition-delay: 1.375s;
  transition: .25s ease-in-out;
}

.language {
  display: flex;
  align-items: center;
  margin-right: 33px;
}

.language-btn {
  position: relative;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  display: block;
  border-radius: 50%;
  background-color: #689a23;
}

.language-btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  content: '';
  transform: translate(-50%, 0);
  border: 4px solid transparent;
  border-top: 4px solid #fff;
}

.language-btn:hover {
  cursor: pointer;
}

.language .lang__item a {
  position: relative;
}

.language .lang__item a::after {
  position: absolute;
  bottom: -2px;
  left: 0;
  content: '';
  display: block;
  height: 1px;
  width: 100%;
  background-color: #fff;
  transform: scale(0);
}

.language .lang__item a:hover::after {
  transition: 0.4s;
  transform: scale(1);
}

.language__list {
  max-height: 20px;
  overflow: hidden;
  line-height: 1.5;
  transition: 0.6s;
}

.language__list-active {
  max-height: 44px;
  height: auto;
}

.section-main {
  position: relative;
  background-color: #777;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.section-main .main-slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}

.section-main .main-slider-img {
  width: 100vw;
  height: 100vh;
}

.section-main .main-slider-img:nth-child(1) {
  height: 100vh;
  background: #eaeaea url("../img/home/home-bg.jpg") center center no-repeat;
  background-size: cover;
}

.section-main .main-slider-img:nth-child(2) {
  height: 100vh;
  background: red url("../img/home/home__about.jpg") center center no-repeat;
  background-size: cover;
}

.section-main .slaierNav {
  padding: 55px 10px;
  padding-left: 180px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 585px;
  min-height: 312px;
  background-color: #ffffff;
  opacity: 0.9;
  box-sizing: border-box;
}

.section-main .slaierNav::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 61px;
  right: 65px;
  width: 30px;
  height: 24px;
  background: url("../img/svg/leaves-w.svg") center center no-repeat;
}

.section-main .slaierNav__title {
  margin-bottom: 25px;
  color: #568F0A;
  font-size: 36px;
  font-weight: 300;
  line-height: 48px;
  text-transform: uppercase;
  letter-spacing: 2.52px;
}

.section-main .slaierNav .btn-slider-next, .section-main .slaierNav .btn-slider-prev {
  margin-right: 15px;
  position: relative;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #568F0A;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-main .slaierNav .btn-slider-next svg, .section-main .slaierNav .btn-slider-prev svg {
  fill: #568F0A;
  stroke: #568F0A;
}

.section-main .slaierNav .btn-slider-next svg .st0, .section-main .slaierNav .btn-slider-prev svg .st0 {
  fill: #568F0A;
  stroke: #568F0A;
}

.section-main .slaierNav .btn-slider-next:hover, .section-main .slaierNav .btn-slider-prev:hover {
  cursor: pointer;
  background-color: #568F0A;
}

.section-main .slaierNav .btn-slider-next:hover svg, .section-main .slaierNav .btn-slider-prev:hover svg {
  fill: #fff;
  stroke: #fff;
}

.section-main .slaierNav .btn-slider-next:hover svg .st0, .section-main .slaierNav .btn-slider-prev:hover svg .st0 {
  fill: #fff;
  stroke: #fff;
}

.section-main .slaierNav .btn-slider-prev {
  transform: rotate(180deg);
}

.section-main .slaierNav .arrows-block {
  position: relative;
  display: flex;
  align-items: center;
}

.section-main .slaierNav .arrows-block::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  right: 120px;
  width: 168px;
  height: 1px;
  background-color: #568F0A;
}

.section-main-down {
  position: absolute;
  bottom: 40px;
  left: 50%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #568F0A;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: .9;
  transform: translateX(-50%);
  animation: bounce 2s infinite;
}

.section-main-down svg {
  width: 27px;
  fill: #fff;
  stroke: #fff;
  transform: rotate(90deg);
}

.section-main-down:hover {
  cursor: pointer;
}

.section-title {
  color: #568F0A;
  font-family: "GothamPro";
  font-size: 30px;
  font-weight: 300;
  line-height: 60px;
  letter-spacing: 2.1px;
}

