@import "smart-grid";
@import "vars";
@import "fonts";
@import "libs";

::placeholder {
	color: #666; }

::selection {
	background-color: $accent;
	color: #fff; }

body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.65;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	background-color: #eaeaea;
	input, textarea {
		border: #666 1px solid;
		outline: none;
		&:focus:required:invalid {
			border-color: red; }
		&:required:valid {
			border-color: green; } } }

b, strong {
	font-weight: bold; }

img {
	width: 100%;
	&:focus {
		outline: none; } }

a {
	text-decoration: none;
	&:hover {
		cursor: pointer; } }
button {
	border: none;
	text-decoration: none;
	&:hover {
		cursor: pointer; } }
.button {
	margin: 0 auto;
	display: block;
	border: none;
	color: #fff;
	text-decoration: none;
	background-color: $accent;
	padding: 15px 45px;
	font-size: 13px;
	text-transform: uppercase;
	font-weight: 600;
	letter-spacing: 3px;
	border-radius: 2px;
	text-align: center;
	position: relative;
	outline: none;
	transition: background-color .1s ease;
	cursor: pointer;

	&::after {
		transition: background-color .2s ease;
		position: absolute;
		content: '';
		height: 4px;
		bottom: 0;
		width: 100%;
		background-color: darken($accent, 50%);
		opacity: .18;
		border-bottom-left-radius: 2px;
		border-bottom-right-radius: 2px;
		left: 0; }
	&:focus, &:hover {
		text-decoration: none;
		color: #fff; }
	&:hover {
		background-color: lighten($accent, 5%);
		&::after {
			opacity: .22; } }
	&:active {
		background-color: darken($accent, 5%);
		&::after {
			opacity: .32; } } }


@include reset();


.debug {
	@include debug(rgba(0,0,0,.5), 1px solid #ff0); }

.container {
	@include wrapper(); }

.productItems {
	@include row-flex();
	justify-content: space-around; }

.productItem {
	padding: 20px;
	margin: 20px 0;
	border-radius: 5px;
	background-color: #fff;

	@include col();
	@include size(4);
	@include size-md(6);
	@include size-xs(12);
	&__img {}

	&__title {
		margin-top: 15px;
		color: $accent;
		font-size: 20px;
		font-weight: 700;
		text-align: center;
		text-transform: uppercase;
		font-weight: 500; }
	&__descr {
		margin: 15px 0;
		font-size: 18px;
		font-weight: 400; }
	button {
		font-weight: 300; } }



.top-menu {
	&-left {
		min-height: 130px;
		display: flex;
		align-items: center;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 10px; }
	&-right {
		min-width: 440px;
		justify-content: space-between;
		display: flex;
		align-items: center;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 10px; } }

.logo {
	position: absolute;
	left: 50%;
	top: 0;
	transform: translateX(-50%); }

.header {
	position: relative;
	min-height: 90px;
	color: #ffffff;
	font-family: "GothamPro";
	font-size: 14px;
	font-weight: 500;
	line-height: 31px;
	letter-spacing: 0.14px;

	&-section {
		background-color: $accent;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 10; }
	a {
		color: #fff; }
	.webcamera {
		padding: 4px 30px;
		margin-right: 33px;
		display: flex;
		align-items: center;
		background-color: #689a23;
		svg {
			margin-right: 15px; } }
	.logo {
		width: 290px;
		height: 170px;
		background: $accent url('../img/logo.svg') center center no-repeat; }
	.gallery {
		display: flex;
		.wrap-icon {
			margin-right: 15px;
			position: relative;
			width: 30px;
			height: 30px;
			margin-right: 15px;
			display: block;
			border-radius: 50%;
			background-color: #689a23;
			display: flex;
			justify-content: center;
			align-items: center; } }
	.tellink {
		white-space: nowrap; }
	.menu-icon {
		user-select: none;
		transition: .25s ease-in-out;
		overflow: hidden;
		&:hover {
			cursor: pointer; } }
	.menu-icon.menu-active {
		.top {
			width: 0%;
			transition-delay: 0s;
			transition: .25s ease-in-out; }
		.middle {
			width: 0%;
			transition-delay: 0.125s;
			transition: .35s ease-in-out; }
		.bottom {
			width: 0%;
			transition-delay: 0.250s;
			transition: .45s ease-in-out; }

		.leaves {
			transform: translateX(100%);
			transition-delay: 0.350s;
			transition: .45s ease-in-out; }
		.word {
			.m {
				transform: translateY(-100%);
				transition-delay: 0s;
				transition: .45s ease-in-out; }
			.e {
				transform: translateY(-100%);
				transition-delay: 0.125s;
				transition: .55s ease-in-out; }
			.n {
				transform: translateY(-100%);
				transition-delay: 0.250s;
				transition: .65s ease-in-out; }
			.u {
				transform: translateY(-100%);
				transition-delay: 0.300s;
				transition: .75s ease-in-out; } }
		.cross_1 {
			width: 100%;
			transform: rotate(45deg) translateY(49%) translateX(2%);
			transform-origin: 50% 50%;
			transition-delay: 1.625s;
			transition: .25s ease-in-out; }

		.cross_2 {
			width: 100%;
			transform: rotate(-45deg) translateY(49%) translateX(-1%);
			transform-origin: 50% 50%;
			transition-delay: 1.375s;
			transition: .25s ease-in-out; } }

	.menu-icon {
		.menuIcon {}

		.top {
			transition-delay: 0.5s;
			transition: .25s ease-in-out; }
		.middle {
			transition-delay: 0.625s;
			transition: .35s ease-in-out; }
		.bottom {
			transition-delay: 0.750s;
			transition: .45s ease-in-out; }

		.leaves {
			transform: translateX(0);
			transition-delay: 0.350s;
			transition: .45s ease-in-out; }
		.word {
			.m {
				transform: translateY(0);
				transition-delay: 0s;
				transition: .45s ease-in-out; }
			.e {
				transform: translateY(0);
				transition-delay: 0.125s;
				transition: .55s ease-in-out; }
			.n {
				transform: translateY(0);
				transition-delay: 0.250s;
				transition: .65s ease-in-out; }
			.u {
				transform: translateY(0);
				transition-delay: 0.300s;
				transition: .75s ease-in-out; } }
		.cross_1 {
			width: 0%;
			transform: rotate(45deg) translateY(49%);
			transform-origin: 50% 50%;
			transition-delay: 1.375s;
			transition: .25s ease-in-out; }

		.cross_2 {
			width: 0%;
			transform: rotate(-45deg) translateY(49%);
			transform-origin: 50% 50%;
			transition-delay: 1.375s;
			transition: .25s ease-in-out; } } }
.language {
	display: flex;
	align-items: center;
	margin-right: 33px;
	&-btn {
		position: relative;
		width: 30px;
		height: 30px;
		margin-right: 15px;
		display: block;
		border-radius: 50%;
		background-color: #689a23;
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			content: '';
			transform: translate(-50%,0);
			border: 4px solid transparent;
			border-top: 4px solid #fff; }
		&:hover {
			cursor: pointer; } }
	.lang__item {
		a {
			position: relative;
			&::after {
				position: absolute;
				bottom: -2px;
				left: 0;
				content: '';
				display: block;
				height: 1px;
				width: 100%;
				background-color: #fff;
				transform: scale(0); }
			&:hover {
				&::after {
					transition: 0.4s;
					transform: scale(1); } } } }
	&__list {
		max-height: 20px;
		overflow: hidden;
		line-height: 1.5;
		transition: 0.6s;
		&-active {
			max-height: 44px;
			height: auto; } }
	&__item {} }

.section-main {
	position: relative;
	background-color: #777;
	height: 100vh;
	width: 100vw;
	overflow: hidden;
	.main-slider {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		&-img {
			width: 100vw;
			height: 100vh;
			&:nth-child(1) {
				height: 100vh;
				background: #eaeaea url('../img/home/home-bg.jpg') center center no-repeat;
				background-size: cover; }
			&:nth-child(2) {
				height: 100vh;
				background: red url('../img/home/home__about.jpg') center center no-repeat;
				background-size: cover; } } }

	.slaierNav {
		padding: 55px 10px;
		padding-left: 180px;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 585px;
		min-height: 312px;
		background-color: #ffffff;
		opacity: 0.9;
		box-sizing: border-box;
		&::after {
			content: "";
			display: block;
			position: absolute;
			bottom: 61px;
			right: 65px;
			width: 30px;
			height: 24px;
			background: url('../img/svg/leaves-w.svg') center center no-repeat; }
		&__title {
			margin-bottom: 25px;
			color: $accent;
			font-size: 36px;
			font-weight: 300;
			line-height: 48px;
			text-transform: uppercase;
			letter-spacing: 2.52px; }
		.btn-slider-next, .btn-slider-prev {
			margin-right: 15px;
			position: relative;
			width: 30px;
			height: 30px;
			margin-right: 15px;
			border-radius: 50%;
			background-color: transparent;
			border: 1px solid $accent;
			display: flex;
			justify-content: center;
			align-items: center;
			svg {
				fill: $accent;
				stroke: $accent;
				.st0 {
					fill: $accent;
					stroke: $accent; } }
			&:hover {
				cursor: pointer;
				background-color: $accent;
				svg {
					fill: #fff;
					stroke: #fff;
					.st0 {
						fill: #fff;
						stroke: #fff; } } } }
		.btn-slider-prev {
			transform: rotate(180deg); }
		.arrows-block {
			position: relative;
			display: flex;
			align-items: center;
			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				right: 120px;
				width: 168px;
				height: 1px;
				background-color: $accent; } } }

	&-down {
		position: absolute;
		bottom: 40px;
		left: 50%;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: $accent;
		display: flex;
		justify-content: center;
		align-items: center;
		opacity: .9;
		transform: translateX(-50%);
		animation: bounce 2s infinite;
		svg {
			width: 27px;
			fill: #fff;
			stroke: #fff;
			transform: rotate(90deg); }
		&:hover {
			cursor: pointer; } } }

.section-title {
	color: $accent;
	font-family: "GothamPro";
	font-size: 30px;
	font-weight: 300;
	line-height: 60px;
	letter-spacing: 2.1px; }
.sectionHome__about {
	padding-top: 70px;
	padding-bottom: 115px; }



@import "media"; // Always at the end
